@import 'styles/base';

:host {
  display: flex;
  background: #FFFFFF;

  box-shadow: 0 10px 20px 5px rgba(0, 0, 0, 0.03);
  border-bottom: 1px solid #ededed;

  .page-block {
    @extend %page-block;

    padding: 10px 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  @media screen and (max-width: 780px) {
    .page-block {
      flex-direction: column;
      align-items: flex-start;
      justify-content: unset;
      padding: 10px 20px;
    }
  }
}
